<template>
  <div>
    <v-row>
      <v-col
        cols="12"
        md="6"
      >
        <material-card
          color="accent"
          full-header
        >
          <template #heading>
            <div
              class="pa-8 white--text text-h4"
            >
              Add New Version
            </div>
          </template>
          <v-container class="px-9">
            <v-text-field
              v-model="version"
              :rules="nameRules"
              label="Version"
              required
            />
            <!-- <ckeditor
              v-model="editorData"
              :editor="editor"
              :config="editorConfig"
            /> -->
            <v-select
              v-model="type"
              :items="typeList"
              label="Type of Update"
              item-text="text"
              item-value="value"
              outlined
            />
            <v-btn
              dark
              class="mt-4"
              color="green"
              @click="save()"
            >
              Save <v-icon class="ml-2">
                mdi-content-save
              </v-icon>
            </v-btn>
          </v-container>
        </material-card>
      </v-col>
      <v-col
        cols="12"
        md="6"
      >
        <material-card
          color="accent"
          full-header
        >
          <template #heading>
            <div
              class="pa-8 white--text text-h4"
            >
              App Version
            </div>
          </template>
          <v-data-table
            :headers="headers"
            :items="data"
            :items-per-page="5"
            class="elevation-1"
          >
            <template v-slot:item.type="{ item }">
              <div
                v-if="item.type === '1'"
              >
                Major Update
              </div>
              <div
                v-if="item.type === '2'"
              >
                Minor Update
              </div>
              <div
                v-if="item.type === '0'"
              >
                Up To Date
              </div>
            </template>
          </v-data-table>
        </material-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
  import axios from 'axios'
  export default {
    data () {
      return {
        data: [],
        editor: ClassicEditor,
        editorData: 'Selamat Datang',
        editorConfig: {
          // The configuration of the editor.
        },
        headers: [
          {
            text: 'Version',
            value: 'version',
          },
          {
            text: 'Type Of Update',
            value: 'type',
          },
          {
            text: 'Date',
            value: 'created_at',
          },
        ],
        version: null,
        type: null,
        typeList: [{
                     text: 'Major Update',
                     value: '1',
                   },

                   {
                     text: 'Minor Update',
                     value: '2',
                   }],
      }
    },

    mounted () {
      this.getData()
    },

    methods: {
      getData () {
        axios.get('get-app-version').then((res) => {
          this.data = res.data.data
        })
      },

      save () {
        axios.post('add-app-version', {
          version: this.version,
          type: this.type,
        }).then((res) => {
          this.type = null
          this.version = null
          this.getData()
          this.$swal('Date created successfully', '', 'success')
        })
      },
    },
  }
</script>
